.tournament {
	position: relative;
	height: 100%;
	width: 100%;
	display: grid;
	grid-template-rows: auto 30.8fr 61.2fr 39px;
	grid-template-columns: 30.8% 38.4% 30.8%;
	justify-items: center;
}

.titleBar {
	grid-column: 2 / span 2;
	grid-row: 1 / span 1;
	white-space: pre-wrap;
	width: 100%;
	position: relative;
	top: 0;
	animation: slide-in-from-top 1s ease-out;
	background-color: #252525;
}

.stage {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-auto-flow: row;
	background-color: rgba(45, 45, 45);
	border-radius: 15px;
	margin: 10px;
}

@keyframes slide-in-from-top {
	from {
		top: -100%;
	}
	top {
		top: 0%;
	}
}

.row {
	grid-column: 1 / span 2;

	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	column-gap: 5px;
	margin: 5px;
	min-height: 32px;
}

.row > *:first-child {
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
}

.row > *:last-child {
	border-top-right-radius: 15px;
	border-bottom-right-radius: 15px;
}

div.header {
	height: 100%;
	background-color: #3c3c3c;
	display: flex;
	align-items: center;
	justify-content: center;
}

div.header > div > div.editableField {
	justify-content: center;
}

div.fixed {
	height: 100%;
	background-color: #3c3c3c;
	display: flex;
	align-items: center;
	justify-content: center;
    box-sizing: border-box;
    border: none;
    padding: 5px;
}

div:global(.button)#previous {
	color: indianred;
}
div:global(.button)#next {
	color: lightgreen;
}

.header svg {
	color: lightslategrey;
}

div.statusBar {
    grid-template-columns: 1fr 4fr 1fr;
}

.form {
	width: auto
}

input.input {
	padding: 0;
	font: 400 16px Arial;
	background-color: none;
}

div.editableField {
	padding: 0;
	border: 2px rgba(0,0,0,0) solid;
	background-color: transparent;
}

div.button {
	padding: 3px;
}

div.button.inactive {
	cursor: default;
	background-color: #3c3c3c;
}

/* Profile */

@keyframes slide-in-from-right {
	from {
		right: -100%;
	}
	to {
		right: 0%;
	}
}

.profile {
	grid-column: 3 / span 1;
    grid-row: 1 / span 4;

	
	background-color: #252526;
	box-sizing: border-box;
	border-top: 2px #252526 solid;
	border-left: 2px #252526 solid;
	border-bottom: 2px #252526 solid;
    height: 100%;
    width: 100%;
    
	position: relative;
	animation: slide-in-from-right 1s ease-out;
	overflow: hidden;
}

.profileStage {
	display: flex;
	flex-direction: column;
	white-space: pre-wrap;
	align-items: flex-start;
	justify-self: stretch;
	align-self: stretch;
	flex-shrink: 0;
	background-color: rgba(45, 45, 45);
	border-radius: 15px;
	margin: 10px;
    height: calc(100% - 20px);
}