.sidebar {
	grid-column: 1 / span 1;
	grid-row: 1 / span 3;
	white-space: pre-wrap;

	background-color: #252526;
	position: relative;
	animation: slide-in-from-left 1s ease-out;
	font-family: Segoe UI Historic, Segoe UI, Helvetica, Arial, sans-serif;
	padding: 10px;
	width: calc(100% - 20px);
}

.stage {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: start;
	justify-self: stretch;
	align-self: stretch;
	background-color: rgba(45, 45, 45);
	border-radius: 15px;
	overflow: hidden;
}

@keyframes slide-in-from-left {
	from {
		left: -100%;
	}
	to {
		left: 0%;
	}
}

.playersSection {
	grid-row: 2 / span 1;
	height: 100%;
	box-sizing: border-box;
	border-radius: 15px;
	overflow-x: hidden;
}

.playersSection::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(85, 85, 85, 0);
}
.playersSection::-webkit-scrollbar:hover {
	background-color: rgba(85, 85, 85, 0.2);
}
.playersSection::-webkit-scrollbar-track {
	background-color: transparent;
}
.playersSection::-webkit-scrollbar-corner {
	display: none;
}
.playersSection::-webkit-scrollbar-button {
	display: none;
}
.playersSection::-webkit-resizer {
	display: none;
}
.playersSection::-webkit-scrollbar-thumb  {
	background-color: rgba(85, 85, 85, 0.8);
	transition: background-color 2s ease;
	border-radius: 15px;
}
.playersSection::-webkit-scrollbar-thumb:hover {
	background-color: rgba(85, 85, 85, 0.8);
}
.playersSection > div {
	margin: 5px;
}


.scoreSection {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	align-self: stretch;
	grid-column: 1 / span 3;
	margin: 5px;
	border-radius: 15px;
	overflow: hidden;
}

.playersSection form {
	border-radius: 15px;
	overflow: hidden;
}

.scoreSection input[type='text'] {
	flex-grow: 1;
}

#addPlayerForm {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.input {
	padding: 8px;
	background-color: #3c3c3c;
	border: none;
	box-shadow: none;
	color: white;
	margin: 0 2px;
}
.scoreSection input, .scoreSection button {
	margin: 0 2px;
}

input[type=text]:focus,
input[type=number]:focus,
input[type=text]:valid,
input[type=number]:valid,
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active {
	background-color: #585858;
}
input[type=submit]:focus,input[type=submit]:active {
	border: none;
	outline: none;
}

input::-webkit-inner-spin-button, 
input::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.playerLine:not(:hover) .edit, .playerLine:not(:hover) .cancel, .playerLine:not(:hover) .activate {
	visibility: hidden;
}

.submit, .edit, .cancel, .activate {
	display: flex;
	align-self: stretch;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	background-image: none;
	cursor: pointer;
	font-weight: bold;
	border: none;
    padding: 0px;
    color: white;   
}

.submit {
	width: 15%;
	background-color: darkgreen;
	color: white;
}

.playerInfo {
	text-align: left;
	justify-content: flex-start;
	box-sizing: border-box;
	font-weight: 500;
}

.edit {
	width: 100%;
	grid-column: 2 / span 1;
	background-color: gray;
}

.activate {
	width: 100%;
	grid-column: 3 / span 1;
	background-color: darkgreen;
}

.cancel {
	width: 100%;
	grid-column: 3 / span 1;
	background-color: darkred;
}

.playerLine {
	display: grid;
	grid-template-columns: calc(70% - 10px) 15% 15%;
	column-gap: 5px;
	width: 100%;
	justify-items: start;
	border-radius: 15px;
	overflow: hidden;
}

.playerInfo:hover {
	background-color: rgb(88, 88, 88);
	z-index: 1;
}

.scoreSeparator {
	display: flex;	
	border: 2px;
	padding: 10px 0 0 0;
}

.sectionHeader {
	display: flex;
	justify-items: flex-start;
	padding: 7px;
	font-size: 13.3333px;
}

.playerInactive {
	color: darkslategrey;
}

form {
	width: 100%;
	padding: 0;
	margin: 0;
	display: flex;
	flex-direction: row;
	align-items: center;
}

/* Bottom Banner */

.topBanner {
	width: 100%;
	align-self: start;
	background-color: #1e1e1e;
	box-sizing: border-box;
	border: 2px #252526 solid;
	padding: 5px;
	white-space: nowrap;
}

.bottomBanner {
    animation: slide-in-from-left 1s ease-out;
    position: relative;
    grid-column: 1 / span 1;
    grid-row: 4 / span 1;
}