.profileContainer {
	height: 100%;
	width: 100%;
}
.profileContainer::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(85, 85, 85, 0);
}
.profileContainer::-webkit-scrollbar:hover {
	background-color: rgba(85, 85, 85, 0.2);
}
.profileContainer::-webkit-scrollbar-track {
	background-color: transparent;
}
.profileContainer::-webkit-scrollbar-corner {
	display: none;
}
.profileContainer::-webkit-scrollbar-button {
	display: none;
}
.profileContainer::-webkit-resizer {
	display: none;
}
.profileContainer::-webkit-scrollbar-thumb  {
	background-color: rgba(85, 85, 85, 0);
	border-radius: 15px;
}
.profileContainer::-webkit-scrollbar-thumb:hover {
	background-color: rgba(85, 85, 85, 0.6);
}

.profileContainer > div {
	margin: 5px;
}

div.bottomBanner {
    animation: slide-in-from-right 1s ease-out;
    position: relative;
    grid-column: 3 / span 1;
	grid-row: 4 / span 1;
	height: unset;
}

div.roundRow {
	grid-template-columns: 30% 70%;
}

.row {
	grid-column: 3 / span 1;
	grid-row: 1 / span 3;
	width: 100%;
	white-space: pre-wrap;
	display: grid;
	grid-template-columns: 38.2% calc(61.8% - 2px);
	column-gap: 2px;
	align-items: stretch;
	margin: 2px 0;
	/*transition: max-height 0.5s ease;*/
}

.resultsRow {
	grid-column: 3 / span 1;
	grid-row: 1 / span 3;
	width: 100%;
	height: 100%;
	white-space: pre-wrap;
	display: grid;
	grid-template-columns: 10% auto 10% 10%;
	column-gap: 2px;
	align-items: start;
	overflow: hidden;
}

div.editable, div.editableField {
	background-color: #4b4b4b;
	display: flex;
	align-items: center;
}

div.editableField, input.input {
	font: 400 13.3333px Arial;
	text-align: left;
}

input.input, div.editable:hover, div.editableField:hover {	
	background-color: #585858;
}

.sectionListWrapper {/*
	overflow: hidden;
	transform: scaleY(-1);*/
	transition: height 0.5s ease;
}
/*
.sectionListWrapper > * {
	transform: scaleY(-1);
}*/

.sectionList {
	position: relative;
	top: 0%;
}

.expanded {
	display: initial;
}

.sectionToggle {
	padding: 0 10px;
    cursor: pointer;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.sectionToggle > * {
    transition: transform 0.25s;
}

div.semiPresent {
	background: repeating-linear-gradient(
		45deg,
		rgb(33, 56, 56),
		rgb(33, 56, 56), 10px,
		rgb(33, 33, 33) 10px,
		rgb(33, 33, 33) 20px
	);
}

div.playerLine {
    grid-template-columns: 85% 15%;
}

input.input {
    width: 85%;
}

button.cancel {
    grid-column: 2 / span 1;
}

.profilePicture {
	width: 100%;
	max-width: 200px;
}

.infoSection {
	border-radius: 15px;
	overflow: hidden;
}

.profileContainer > div {
	margin: 5px;
}