.pairings {
    grid-column: 2 / span 1;
	grid-row: 2 / span 2;
	height: 100%;
	width: 100%;
	background-color: #252525;
	animation: fade-in 2s ease-out;
}

.stage {
	height: calc(100% - 10px);
	display: flex;
	flex-direction: column;
	overflow: scroll;
	margin: 0 10px 10px 10px;
	border-radius: 15px;
	background-color: rgb(45, 45, 45);
	animation: fade-in 1s ease-out;
}

.pairingContainer {
    height: 100%;
}
.pairingContainer::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(85, 85, 85, 0.3);
}
.pairingContainer::-webkit-scrollbar-track {
	background-color: transparent;
}
.pairingContainer::-webkit-scrollbar-corner {
	display: none;
}
.pairingContainer::-webkit-scrollbar-button {
	display: none;
}
.pairingContainer::-webkit-resizer {
	display: none;
}
.pairingContainer::-webkit-scrollbar-thumb  {
	background-color: rgba(85, 85, 85, 0.5);
	transition: background-color 2s ease;
	border-radius: 15px;
}
.pairingContainer::-webkit-scrollbar-thumb:hover {
	background-color: rgba(85, 85, 85, 0.8);
}

.pairing {
	display: grid;
	grid-template-columns: 3fr 135px 3fr;
	font: 400 13.3333px Arial;
	justify-items: center;
	align-items: center;
	animation: fade-in 1s ease-out;
	justify-self: stretch;
	box-sizing: border-box;
	border-bottom: 1px rgba(0, 0, 0, 0.1) solid;
}
.pairing > div:first-child {
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
}
.pairing > div:last-child {
	border-top-right-radius: 15px;
	border-bottom-right-radius: 15px;
}
.pairing:hover .playerInfo, .pairing:hover > form {
	background-color: #585858;
}
.pairing:not(:hover) input {
	visibility: hidden;
}
.pairing form:focus input.inputResult {
	visibility: visible;
}

.titleContainer {
	animation: fade-in 2s ease-out;
}

div.topBanner {	
	margin: 5px;
	width: unset;
}

@keyframes fade-in {
	0% {
		opacity: 0
	}
	50% {
		opacity: 0
	}
	100% {
		opacity: 1
	}
}

div.playerInfo {
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
}

.statusBarBackground {
	grid-column: 2 / span 1;
}

div.statusBar {
	display: grid;
    grid-template-columns: 1fr 4fr 1fr;
	column-gap: 5px;
	position: relative;
	animation: slide-in-from-bottom 1s ease-out;
}

@keyframes slide-in-from-bottom {
	0% {
		bottom: -100%;
	}
	50% {
		bottom: -100%;
	}
	100% {
		bottom: 0%;
	}
}

.waitButton, .finishButton, .nextButton {
    display: flex;
	align-items: center;
	justify-content: center;
}

div.waitButton {
	cursor: default;
	background-color: #1e1e1e;
}

div.nextButton {
	width: 100%;
	background-color: darkgreen;
	justify-self: center;
}

.nextButton > svg {
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg)
    }
    50% {
        transform: rotate(180deg)
    }
    100% {
        transform: rotate(360deg)
    }
}

.inputResult {
	padding: 5px;
	background-color: #585858;
	box-sizing: border-box;
	border: 2px #252526 solid;
	white-space: nowrap;
	color: white;
	justify-content: center;
    text-align: center;
    align-items: center;
}

.inputResult {
	background-color: #585858;
}

.bye {
	background-color: rgb(36, 36, 36) !important;
}

.submit {
	display: flex;
	align-self: stretch;
	flex-direction: row;
	justify-content: center;
	background-image: none;
	cursor: pointer;
	box-sizing: border-box;
	border: 2px #252526 solid;
	padding: 0px;
	width: 100%;
	background-color: darkgreen;
	color: white;
}