body {
	color: white;
	justify-items: center;
}
*:not(.scrollable)::-webkit-scrollbar {
	display: none;
}
*:not(.scrollable) {
	scrollbar-width: none;
}

.scrollable {
	overflow-x: hidden;
	overflow-y: auto;
	width: 100%;
}
.scrollable::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(85, 85, 85, 0);
}
.scrollable::-webkit-scrollbar:hover {
	background-color: rgba(85, 85, 85, 0.2);
}
.scrollable::-webkit-scrollbar-track {
	background-color: transparent;
}
.scrollable::-webkit-scrollbar-corner {
	display: none;
}
.scrollable::-webkit-scrollbar-button {
	display: none;
}
.scrollable::-webkit-resizer {
	display: none;
}
.scrollable::-webkit-scrollbar-thumb  {
	background-color: rgba(85, 85, 85, 0.5);
	transition: background-color 2s ease;
}
.scrollable::-webkit-scrollbar-thumb:hover {
	background-color: rgba(85, 85, 85, 0.8);
}

button > * {
    pointer-events: none;
}

.stage {
	height: calc(100vh - 30px);
	width: 100vw;
	border: 0px;
	position: fixed;
	top: 30px;
	text-align: center;
	overflow: scroll;
	background-color: #111;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

button.button, div.button {	
	display: flex;
	justify-self: stretch;
	height: 100%;
	white-space: pre-wrap;
	box-sizing: border-box;
	background-color: #1e1e1e;
	padding: 0;
	justify-content: center;
	align-content: center;
	align-items: center;
	cursor: pointer;
	-webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

.button {
	font-size: 14px;
	height: 100%;
    display: flex;
    flex-direction: row;
	align-items: center;
}

.button > svg {
	padding: 0 5px;
}

div.button:hover, button.button:hover {
	background-color: rgb(69, 69, 69);
}

div.fieldBox {
	display: flex;
	text-align: left;
	align-items: center;
    background-color: rgb(33, 56, 56);
	padding: 8px;
	white-space: break-word;
	overflow-x: scroll;
	font: 400 13.3333px Arial;
	cursor: default;
}

div.playerInfo {
	display: flex;
	justify-content: flex-start;
	align-content: center;
	width: calc(100%);/*
	background-color: rgb(66, 66, 66);
	border: 2px #252526 solid;*/
	padding: 8px;
	white-space: break-word;
	height: 100%;
	font-size:13.3333px;
	cursor: pointer;
}

div.playerInfo:hover {
	background-color: rgb(88, 88, 88);
}

div.topBanner {
	width: 100%;
	align-self: start;
	background-color: #1e1e1e;
	box-sizing: border-box;
	padding: 5px;
	border-radius: 15px;
	min-height: 32px;
}

div.bottomBannerBackground {
	background-color: #252525;
	width: calc(100% - 20px);
	padding: 0 10px 10px 10px;
}

div.bottomBanner {
    height: 100%;
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	column-gap: 5px;
	width: 100%;
	align-self: end;
	box-sizing: border-box;
	justify-items: center;
	justify-content: center;
	border-radius: 15px;
	overflow: hidden;
}

input[type='submit'] {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
}

.input {
	display: flex;
	padding: 5px 0 5px 5px;
	background-color: #585858;
	box-sizing: border-box;
	border: 2px #252526 solid;
	box-shadow: none;
	color: white;
}