.gridPage {
	grid-column: 1 / span 3;
	grid-row: 2 / span 3;
	width: 100%;
}
.gridPage::-webkit-scrollbar {
	width: 10px;
	background-color: rgba(85, 85, 85, 0.3);
}
.gridPage::-webkit-scrollbar-track {
	background-color: transparent;
}
.gridPage::-webkit-scrollbar-corner {
	display: none;
}
.gridPage::-webkit-scrollbar-button {
	display: none;
}
.gridPage::-webkit-resizer {
	display: none;
}
.gridPage::-webkit-scrollbar-thumb  {
	background-color: rgba(85, 85, 85, 0);
	transition: background-color 2s ease;
}
.gridPage::-webkit-scrollbar-thumb:hover {
	background-color: rgba(85, 85, 85, 0.6);
}

.titles {
	grid-column: 1 / span 2;
	justify-self: stretch;
	display: flex;
	flex-direction: row;
	justify-items: flex-start;
	background-color: #1e1e1e;
	padding: 5px;
	position: sticky;
}

.titles > div {
	background-color: rgb(45, 45, 45);
	border-radius: 15px;
	padding: 8px 16px;
	margin: 0 5px;
	height: 100%;
	font-size: 14px;
}

.table {
	margin: 10px 20px;
}

.td, .th {
	padding: 0;
}

.cell {
	display: flex;
	align-content: center;
	background-color: #1e1e1e;
	border: 1px #252526 solid;
	padding: 8px 15px 8px 15px;
	white-space: nowrap;
	font: 400 13.3333px Arial;
	cursor: default;
	justify-content: center;
}

.blank {
	background-color: transparent;
	border: none;
}

.player:hover {
	background-color: #3c3c3c;
}

.edit {
	background-color: rgb(33, 56, 56);
}

div.headerCell, div.firstColumn {
	font-weight: bold;
	background-color: transparent;
	border: none;
}

div.closeButton {
	margin-left: 30px;
	justify-self: flex-end;
}

.container {
    grid-column: 1 / span 2;
    margin: 30px;
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    min-width: calc(100% - 60px);
}

.textarea, .form {
    width: 100%;
    height: 100%;
}

.form {
    box-sizing: border-box;
    display: grid;
    grid-auto-flow: column;
    grid-template-rows: 90% 10%;
}

.textarea {
    box-sizing: border-box;
    background-color: rgb(33, 33, 33);
    min-height: 60vh;
    color: white;
}

.submit {
    width: 100%;
}

/* SCROLLBAR */

.scrollbar {
	position: absolute;
	width: 20px;
	z-index: 1;
	background-color: white;
}

.track {
	z-index: 2;
	width: 100%;
	background-color: black;
	height: 40px;
}